.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header-logo {
  height: 150px;
}

.header-logo-socials {
  height: 50px;
  margin: 5px;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 20px;
}

.navbar-items {
  color: rgb(255, 255, 255);
  text-decoration: none;
  padding: 10px;
  width: 25%;
  border: 1px solid white;
}

.navbar-links {
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.navbar-links:hover {
  color: #555;
}

.video-div {
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
  flex-wrap: wrap;
  margin: 15px;
}

.video-card {
  width: 400px;
  /* background-color: rgb(255, 255, 255); */
  color: rgb(255, 255, 255);
}

.video {
  border: 2px solid white;
  width: 350px;
}

.quick-links-links {
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  line-height: 1.5;
}

.contact-links {
  color: rgb(255, 255, 255);
  font-size: 1.5rem;
  line-height: 1.5;
}

.footer {
  background-color: rgb(0, 0, 0);
  height: 75px;
  width: 100%;
}
